/* eslint-disable no-alert */
import React, { useRef, useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { Link } from 'gatsby'
import axios from 'axios'

import Loader from '../../layouts/Loader'

import helpers from '../../../helpers'
import * as actions from '../../../redux/actions'

import logoPath from '../../../images/logoLogin.svg'

import styles from './Login.module.scss'

axios.defaults.withCredentials = true

const { linksPaths } = helpers.constants.links

function Login({ authMe }) {
    const loginField = useRef(null)
    const passwordField = useRef(null)

    const [loaderState, setLoaderState] = useState(false)

    useEffect(() => {
        const timeout = setTimeout(() => {
            setLoaderState(false)
        }, 10000)
        return () => {
            clearTimeout(timeout)
        }
    }, [loaderState])

    return loaderState
        ? <Loader />
        : (
            <div className={styles.layout}>
                <header className={styles.header}>
                    <Link to={linksPaths.root}>
                        <img src={logoPath} alt="logo" />
                    </Link>
                </header>
                <form
                    className={styles.form}
                    onSubmit={(e) => {
                        e.preventDefault()
                        authMe(
                            loginField.current.value,
                            passwordField.current.value,
                        )
                        setLoaderState(true)
                    }}
                >
                    <label className={styles.form__label}>
                        <span>Логин</span>
                        <input className={styles.form__input} type="text" name="login" id="login" ref={loginField} />
                    </label>
                    <label className={styles.form__label}>
                        <span>Пароль</span>
                        <input className={styles.form__input} type="password" name="password" id="password" ref={passwordField} />
                    </label>
                    <button className={styles.form__button} type="submit">
                    Войти
                    </button>
                </form>
            </div>
        )
}

const ConnectedLogin = connect(
    null,
    dispatch => ({
        authMe: (username, password) =>
            dispatch(actions.AuthActions.default.askAuth({ username, password })),
    }),
)(Login)

export default ConnectedLogin
